:root {
	--color-primary: #006289 !important;
	--color-secondary: #f3f4f6 !important;
	--color-tertiary: #dee2e6 !important;
	--color-light: #fff;
	--color-dark: #0f172a;
	--color-dark-theme: #111;
	--color-dark-primary: #222;
	--color-dark-secondary: #444;
	--color-border: #555;
	--color-danger: #ff0000;
}
* {
	box-sizing: border-box;
}

.tab-wrapper {
	display: grid;
	grid-template-columns: 45% 55%;
	gap: 20px;
	@media screen and (max-width: "850px") {
		display: flex;
		flex-direction: column;
	}
	.mdc-tab-scroller {
		display: inherit !important;
		height: 400px;
		overflow: auto;
		scrollbar-width: thin;
		border-radius: 4px;
		border: 1px solid #dee2e6;
		&::-webkit-scrollbar {
			width: 0.5em;
			height: 0.5em;
		}
		&::-webkit-scrollbar-track {
			background-color: var(--color-tertiary);
			margin-block: 0.5em;
		}
		&::-webkit-scrollbar-thumb {
			background: var(--color-primary);
		}
		&::-webkit-scrollbar-thumb:hover {
			opacity: 0.2;
		}
		@media screen and (max-width: "850px") {
			max-height: 200px;
		}
	}
	.mdc-tab-scroller__scroll-area {
		display: inherit !important;
	}
	.mdc-tab-scroller__scroll-content {
		display: inherit;
		border: 1px solid var(--color-tertiary);
		.mdc-tab:not(:last-child) {
			border-bottom: 1px solid var(--color-tertiary);
		}
	}
	.brand-tab-screen {
		display: none;
		&.brand-tab-screen--active {
			display: block;
			p {
				padding: 15px;
				border-radius: 5px;
				border: 1px solid #dee2e6;
				margin: 0;
				color: #000;
				text-align: justify;
				height: 400px;
				overflow: auto;
				scrollbar-width: thin;
				background-color: #fff;
				scrollbar-width: thin;
				&::-webkit-scrollbar {
					width: 0.5em;
					height: 0.5em;
				}
				&::-webkit-scrollbar-track {
					background-color: var(--color-tertiary);
					margin-block: 0.5em;
				}
				&::-webkit-scrollbar-thumb {
					background: var(--color-primary);
				}
				&::-webkit-scrollbar-thumb:hover {
					opacity: 0.2;
				}
				@media screen and (max-width: "850px") {
					max-height: 200px;
				}
			}
			.ask__tab__content {
				display: block;
			}
		}
	}
	.wp-block-create-block-tabquestion {
		p {
			display: block !important;
		}
	}
	.mdc-tab {
		text-align: left;
		padding: 10px;
		justify-content: flex-start;
		border: none;
		outline: none;
		cursor: pointer;
		padding: 14px 16px;
		transition: 0.3s;
		position: relative;
		background-color: #fff;
		&:hover {
			background-color: #f3f4f6;
		}
		&.mdc-tab--active {
			position: relative;
		}
		.mdc-tab-indicator--active {
			background-color: #dee2e6;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
		.new-tab-add {
			&.mdc-tab-indicator--active {
				background-color: #fff;
				&:hover {
					background-color: #f3f4f6;
				}
			}
		}
		&__content,
		.components-button {
			position: relative;
			z-index: 99;
		}
	}
	.mdc-tab.mdc-ripple-upgraded,
	.mdc-tab.mdc-tab--active {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.ask__tab__question--text {
		padding: 15px;
		border-radius: 5px;
		border: 1px solid #dee2e6;
		margin: 0;
		color: #000;
		text-align: justify;
		height: 400px;
		overflow: auto;
		scrollbar-width: thin;
		background-color: #fff;
		scrollbar-width: thin;
		&::-webkit-scrollbar {
			width: 0.5em;
			height: 0.5em;
		}
		&::-webkit-scrollbar-track {
			background-color: var(--color-tertiary);
			margin-block: 0.5em;
		}
		&::-webkit-scrollbar-thumb {
			background: var(--color-primary);
		}
		&::-webkit-scrollbar-thumb:hover {
			opacity: 0.2;
		}
		@media screen and (max-width: "850px") {
			max-height: 200px;
		}
	}
}
.mdc-tab-bar {
	position: relative;
}
.mdc-tab__ripple.plus-btn {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	height: 100%;
	overflow: hidden;
	will-change: transform, opacity;
	width: 30px;
	height: 30px;
	background: #dee2e6;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	z-index: 11;
}

.ask__tab__question--text {
	display: flex;
	flex-direction: column;
	gap: 10px;
	align-items: center;
}

.ask {
	// audio player
	&__audio {
		padding: 30px;
		padding-top: 40px;
		height: 400px;
		border-radius: 5px;
		background-color: var(--color-tertiary);
		display: flex;
		flex-direction: column;
		justify-content: center;
		@media screen and (max-width: "850px") {
			padding: 20px;
			padding-top: 30px;
			height: 300px;
		}
		.details {
			display: flex;
			align-items: center;
			flex-direction: column;
			justify-content: center;
		}
		.buttons {
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.active {
			color: var(--color-dark);
		}
		.playpause-track {
			cursor: pointer;
			background-color: var(--color-primary);
			transition: all 0.5s;
			width: 80px;
			height: 80px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				fill: var(--color-light);
			}
			&:hover {
				background-color: var(--color-dark);
			}
		}
		.slider_container {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;
		}
		.seek_slider,
		.volume_slider {
			appearance: none;
			height: 5px;
			background: var(--color-primary);
			transition: opacity 0.2s;
			cursor: pointer;
		}
		.seek_slider:hover,
		.volume_slider:hover {
			opacity: 1;
		}
		.seek_slider {
			width: 60%;
		}

		.volume_slider {
			width: 30%;
		}
		.seek_slider::-webkit-slider-thumb,
		.volume_slider::-webkit-slider-thumb {
			appearance: none;
			width: 15px;
			height: 15px;
			background: var(--color-light);
			border: 3px solid var(--color-dark);
			cursor: grab;
			border-radius: 100%;
		}

		.loader {
			height: 70px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 25px;
			&.animatedPlay {
				.stroke {
					animation: inherit;
				}
			}
		}
		.loader .stroke {
			background: var(--color-light);
			height: 150%;
			width: 10px;
			border-radius: 50px;
			margin: 0 5px;
			animation: animate 1.4s linear infinite;
		}
		@keyframes animate {
			50% {
				height: 20%;
				background: var(--color-primary);
			}

			100% {
				background: var(--color-primary);
				height: 100%;
			}
		}
		.stroke:nth-child(1) {
			animation-delay: 0s;
		}
		.stroke:nth-child(2) {
			animation-delay: 0.3s;
		}
		.stroke:nth-child(3) {
			animation-delay: 0.6s;
		}
		.stroke:nth-child(4) {
			animation-delay: 0.9s;
		}
		.stroke:nth-child(5) {
			animation-delay: 0.6s;
		}
		.stroke:nth-child(6) {
			animation-delay: 0.3s;
		}
		.stroke:nth-child(7) {
			animation-delay: 0s;
		}
	}
	// video player
	&__tab {
		&__search {
			position: relative;
			margin-bottom: 15px;
			input {
				width: 100%;
				padding: 15px 15px !important;
				border: none;
				border: 1px solid var(--color-tertiary);
				font-size: 16px !important;
				height: auto !important;
				border-radius: 4px;
				font-family: "Poppins", sans-serif;
				&:focus {
					outline: none;
				}
			}
			span {
				position: absolute;
				top: 16px;
				right: 15px;
				cursor: pointer;
				color: var(--color-dark);
			}
		}
		&__content {
			display: none;
			height: 400px;
			&.ask-active {
				display: block;
			}
			@media screen and (max-width: "850px") {
				max-height: 400px;
				video {
					max-height: 400px;
				}
			}
			@media screen and (max-width: "650px") {
				max-height: 320px;
				height: auto;
			}
			video {
				width: 100%;
				height: 100%;
				border-radius: 5px;
				object-fit: cover;
			}
			p {
				padding: 15px;
				border-radius: 5px;
				border: 1px solid var(--color-tertiary);
				margin: 0;
				color: var(--color-dark);
				text-align: justify;
				height: 400px;
				overflow: auto;
				scrollbar-width: thin;
				background-color: var(--color-light);
				@media screen and (max-width: "850px") {
					height: 300px;
				}
				&::-webkit-scrollbar {
					width: 0.5em;
					height: 0.5em;
				}
				&::-webkit-scrollbar-track {
					background-color: var(--color-tertiary);
					margin-block: 0.5em;
				}
				&::-webkit-scrollbar-thumb {
					background: var(--color-primary);
				}
				&::-webkit-scrollbar-thumb:hover {
					opacity: 0.2;
				}
			}
		}
	}
}
.no-question-found {
	text-align: center;
	padding: 10px 0;
	color: var(--color-danger);
	background-color: var(--color-light);
}

.theme-dark {
	svg {
		fill: var(--color-light);
	}
	.plus-btn {
		svg {
			fill: var(--color-dark);
		}
	}
	.tab-wrapper {
		.brand-tab-screen.brand-tab-screen--active p {
			border-color: var(--color-border);
			color: var(--color-light);
			background-color: var(--color-dark-primary);
		}
		.mdc-tab {
			background-color: var(--color-dark-primary);
			border-color: var(--color-border);
			color: var(--color-light);
			&:hover {
				background-color: var(--color-dark-secondary);
			}
		}
		.mdc-tab .mdc-tab-indicator--active {
			background-color: var(--color-dark-secondary);
		}
		.mdc-tab-scroller {
			border-color: var(--color-border);
			background-color: var(--color-dark-primary);
		}
		.mdc-tab-scroller__scroll-content {
			border-color: var(--color-border);
		}
		.mdc-tab-scroller__scroll-content .mdc-tab:not(:last-child) {
			border-color: var(--color-border);
		}
	}
	.ask {
		&__audio {
			background-color: var(--color-dark-primary);
			color: var(--color-light);
		}
		&__tab {
			&__search {
				input {
					background-color: var(--color-dark-secondary);
					color: var(--color-light);
					border-color: var(--color-border);
					&::placeholder {
						color: var(--color-light);
					}
					&:focus {
						background-color: var(--color-dark-secondary);
						color: var(--color-light);
						border-color: var(--color-border);
					}
				}
				span {
					color: var(--color-light);
				}
			}
		}
		.ask-accordion .ask__audio .loader .stroke {
			background-color: var(--color-dark-secondary);
		}
	}
	.no-question-found {
		background-color: var(--color-dark-primary);
	}
}
